





















export default {
	data() {
		return {
			title: 'map',
			latitude: 23.330154,
			longitude: 116.150986,
			covers: [{
				latitude: 23.330154,
				longitude: 116.150986,
				iconPath: '../../static/location.png',
				width: 30,
				height: 30,
				title:"荣兴木业",
				label:{
					content: "荣兴木业",
					fontSize: 12
				}
			}],
			name: "荣兴木业",
			address: "广东省揭阳普宁市燎原街道普师高级中学西300米",
		}
	},
	computed: {
		info() {
			let self = this
			return [{
					tag: "公司名称",
					name: self.name
				},
				{
					tag: "地址",
					name: self.address,
					isAddress: true
				},
				{
					tag: "座机",
					name: "(0663)2285847",
					isCall: true
				},
				{
					tag: "手机",
					name: "13531994925",
					isCall: true
				}
			]
		}
	},
	mounted() {},
	methods: {
		openMap(latitude, longitude) {
			let self = this
			uni.openLocation({
				latitude,
				longitude,
				name: self.name,
				address: self.address,
				success: function() {
					console.log('success');
				}
			});
		},
		call(obj) {
			let self = this
			if (obj.isCall){
				uni.makePhoneCall({
					phoneNumber: obj.name
				});
			}
			if (obj.isAddress){
				this.openMap(self.latitude,self.longitude)
			}
		}
	}
}
